@import "./../../banner.scss";
@import "./../../products.scss";
@import "./../../text-block-with-swiper.scss";
@import "./../../info-page.scss";


.catalog-products__main {
  position: relative;
  .banner-container__title {
    @media (min-width: $tablet) {
      margin-top: 16px;
    }
  }
  .body-contacts {
    @media (min-width: $tablet) {
      padding-top: 90px;
    }
  }
  .products-filter-section {
    padding-top: 70px;
    padding-bottom: 95px;
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .product-filter-block-with-buttons {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .product-filter-block {
        display: flex;
        gap: 69px;
      }
      .product-filter-block__title {
        display: flex;
        align-items: center;
        gap: 10px;
        @include rale-700-17;
        .icon-settings {
          font-size: 22px;
          color: $darkGreen;
        }
      }
      .product-filter-block__filters-list {
        display: flex;
        align-items: center;
        gap: 28px;
        .custom-select.product-filter-block__filters-item {
          width: 100%;
          select {
            padding: 20px;
          }
          &.filter-1 {
            .select-selected {
              width: 283px;
            }
            @media (max-width: 1200px) {
              display: none;
            }
          }
          &.filter-2 {
            .select-selected {
              width: 171px;
            }
            @media (max-width: 1200px) {
              display: none;
            }
          }
          .select-selected {
            padding-top: 12px;
            padding-bottom: 10px;
            padding-left: 15px;
            padding-right: 17px;
            display: flex;
            justify-content: space-between;
            &::after {
              font-size: 7px;
              color: $darkGreen;
            }
          }
        }
        .filter-3 {
          @include rale-700-17;
          display: flex;
          align-items: center;
          padding-left: 24px;
          padding-right: 24px;
          gap: 12px;
          @media (max-width: 700px) {
            display: none;
          }
          span {
            display: flex;
            align-items: center;
          }
          .icon-arrow {
            color: $gray;
            &.active {
              color: $darkGreen;
            }
            &:first-child {
              &::before {
                transform: rotate(90deg);
              }
            }
            &:last-child {
              &::before {
                margin-left: 5px;
              }
            }
            width: 15px;
            height: 15px;
            position: relative;
            &::before {
              font-size: 13px;
              top: 0;
              left: 0;

              position: absolute;
              transform: rotate(-90deg);
            }
            &:hover {
              color: $darkGreen;
              cursor: pointer;
            }
          }
        }
      }

      .buttons-block {
        display: flex;
        align-items: center;
        gap: 40px;

        .rectangle.background {
          padding: 15px 33px 15px 33px;
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
          @media (max-width: 700px) {
            display: none;
          }
        }
        .text {
          .icon-close {
            margin-right: 8px;
          }
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
      }
    }

    .products-filter-section__button {
      margin: auto;
      display: block;
      margin-top: 70px;
      padding: 17px 46px 17px 46px;
      border-width: 2px;
      @media (max-width: $mobile) {
        margin-top: 30px;
      }
    }
    .products__products-list.products-list {
      margin-top: 80px;
      @media (max-width: $mobile) {
        margin-top: 30px;
      }
    }
  }
}
