.products-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 410px));
  justify-content: center;
  align-items: center;
  row-gap: 50px;
  column-gap: 30px;
}
@media (max-width: 1150px) {
  .products-list {
    grid-template-columns: repeat(2, minmax(300px, 410px));
  }
}
@media (max-width: 800px) {
  .products-list {
    grid-template-columns: repeat(1, minmax(300px, 610px));
  }
}
@media (max-width: 600px) {
  .products-list {
    row-gap: 10px;
  }
}
.products-list .product-item {
  width: 100%;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.08);
}
.products-list .product-item.promotion::before {
  color: #dd133b;
}
.products-list .product-item.promotion::after {
  color: #ffffff;
  content: "Акция";
}
.products-list .product-item.novelty::before {
  color: #e1843e;
}
.products-list .product-item.novelty::after {
  color: #ffffff;
  content: "Новинка";
}
.products-list .product-item.promotion::after, .products-list .product-item.promotion::before, .products-list .product-item.novelty::after, .products-list .product-item.novelty::before {
  top: 20px;
  left: -2px;
}
.products-list .product-item.promotion:before, .products-list .product-item.novelty:before {
  position: absolute;
  font-family: icomoon;
  font-size: 33px;
  line-height: 33px;
  content: "\e926";
  z-index: 2;
}
.products-list .product-item.promotion::after, .products-list .product-item.novelty::after {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 31px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 8px;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  line-height: 33px;
  z-index: 2;
}
.products-list .product-item__swiper {
  position: relative;
  overflow: hidden;
}
.products-list .product-item__name {
  margin-top: 17px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  letter-spacing: 0.01em;
  padding-left: 21px;
  padding-right: 62px;
}
.products-list .product-item__code {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  letter-spacing: 0.01em;
  padding-left: 22px;
  color: #595959;
}
.products-list .product-item__type {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 0.01em;
  margin-top: 9px;
  padding-left: 22px;
  padding-right: 62px;
  line-height: 20px;
}
.products-list .product-item__additional-information {
  display: flex;
  justify-content: space-between;
  margin-top: 17px;
}
.products-list .product-item__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.08);
